import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import s from './ModalRegistration.module.css';
import { signupOperation } from '../../redux/session/sessionOperations';
import { closeModal } from '../../redux/ModalRegistration/ModalRegistrationActions';
import { getErrorMessageRegistration } from '../../redux/sessionLogin/sessionLoginSelectors';
import IconsAvatar from '../IconAvatar/IconAvatar';
import { ReactComponent as OpenEye } from '../../assets/svg/openEye.svg';
import { ReactComponent as CloseEye } from '../../assets/svg/closeEye.svg';

class ModalRegistration extends Component {
  static propTypes = {
    onSignUp: PropTypes.func.isRequired,
    closeModal: PropTypes.func.isRequired,
    errorMessage: PropTypes.string,
  };

  static defaultProps = {
    errorMessage: '',
  };

  state = {
    name: '',
    age: '',
    email: '',
    password: '',
    rePassword: '',
    showPassword: 'password',
    errorRePassword: '',
    avatar: 'https://go-to-goal.goit.co.ua/image/avatar_008.png',
    formErrors: { name: '', age: '', email: '', password: '' },
    formValid: false,
    nameValid: false,
    ageValid: false,
    emailValid: false,
    passwordValid: false,
  };

  handleChange = ({ target }) => {
    const { name, value } = target;
    this.setState({ [name]: value }, () => this.validateField(name, value));
  };

  handleSubmitForm = e => {
    e.preventDefault();

    const { name, age, email, password, rePassword, avatar } = this.state;
    const { onSignUp } = this.props;
    if (password === rePassword) {
      onSignUp({
        name,
        email,
        password,
        age,
        avatar,
        isChild: true,
      });
    } else {
      this.setState({
        errorRePassword: '  !!!',
      });
    }
  };

  validateField = (fieldName, value) => {
    const {
      formErrors,
      nameValid,
      ageValid,
      emailValid,
      passwordValid,
    } = this.state;
    const fieldValidationErrors = formErrors;
    let fieldNameValid = nameValid;
    let fieldAgeValid = ageValid;
    let fieldEmailValid = emailValid;
    let fieldPasswordValid = passwordValid;
    switch (fieldName) {
      case 'name':
        fieldNameValid =
          // eslint-disable-next-line no-useless-escape
          /^[a-zA-Z--\s]{2,12}$/.test(value);
        fieldValidationErrors.name = fieldNameValid
          ? ''
          : ",   i i' i 2  12 i,  i i i...";
        break;

      case 'age':
        fieldAgeValid =
          // eslint-disable-next-line no-useless-escape
          /^\d+$/.test(value) && value >= 3 && value <= 99;
        fieldValidationErrors.age = fieldAgeValid
          ? ''
          : `,  i   i 3  99 i :)`;
        break;

      case 'email':
        fieldEmailValid =
          // eslint-disable-next-line no-useless-escape
          /^([a-z0-9_\.-]+)@([a-z0-9_\.-]+)\.([a-z\.]{2,6})$/.test(value);
        fieldValidationErrors.email = fieldEmailValid
          ? ''
          : ',  email   i...';
        break;

      case 'password':
        fieldPasswordValid = value.length >= 6 && value.length <= 12;
        fieldValidationErrors.password = fieldPasswordValid
          ? ''
          : ',   i  i 6  12 i...';
        break;
      default:
        break;
    }
    this.setState(
      {
        formErrors: fieldValidationErrors,
        nameValid: fieldNameValid,
        ageValid: fieldAgeValid,
        emailValid: fieldEmailValid,
        passwordValid: fieldPasswordValid,
      },
      this.validateForm,
    );
  };

  validateForm = () => {
    const { nameValid, ageValid, emailValid, passwordValid } = this.state;
    this.setState({
      formValid: nameValid && ageValid && emailValid && passwordValid,
    });
  };

  handleCloseModal = () => {
    // eslint-disable-next-line no-shadow
    const { closeModal } = this.props;

    closeModal();
  };

  changeUserPic = avatar => {
    return this.setState({ avatar });
  };

  onShowPassword = () => {
    this.setState(prevState => ({
      showPassword: prevState.showPassword === 'password' ? 'text' : 'password',
    }));
  };

  render() {
    const {
      name,
      age,
      email,
      password,
      showPassword,
      rePassword,
      errorRePassword,
      formErrors,
      formValid,
      nameValid,
      ageValid,
      emailValid,
      passwordValid,
    } = this.state;
    const { errorMessage } = this.props;
    return (
      <div className={s.reg_container}>
        <h1 className={s.title_h1}></h1>
        <div className={s.form_container}>
          <form className={s.form} onSubmit={this.handleSubmitForm}>
            <h2 className={s.title_h2}></h2>
            <div className={s.box_input}>
              <input
                type="text"
                name="name"
                value={name}
                onChange={this.handleChange}
                placeholder="  i'..."
              />
              <input
                type="number"
                name="age"
                value={age}
                onChange={this.handleChange}
                placeholder="  ..."
              />
              <input
                type="email"
                name="email"
                value={email}
                onChange={this.handleChange}
                placeholder=" i email/i..."
              />
              <div className={s.box_showPassword}>
                <input
                  type={showPassword}
                  name="password"
                  value={password}
                  onChange={this.handleChange}
                  placeholder=" i ..."
                />
                <button
                  type="button"
                  onClick={this.onShowPassword}
                  className={s.btn_eye}
                >
                  {showPassword === 'text' ? (
                    <CloseEye className={s.eye} />
                  ) : (
                    <OpenEye className={s.eye} />
                  )}
                </button>
              </div>
              <div className={s.box_showPassword}>
                <input
                  type={showPassword}
                  name="rePassword"
                  value={rePassword}
                  onChange={this.handleChange}
                  placeholder=" ..."
                />
                <button
                  type="button"
                  onClick={this.onShowPassword}
                  className={s.btn_eye}
                >
                  {showPassword === 'text' ? (
                    <CloseEye className={s.eye} />
                  ) : (
                    <OpenEye className={s.eye} />
                  )}
                </button>
              </div>
            </div>

            {!formValid && !nameValid && (
              <i className={s.error_name}>{formErrors.name}</i>
            )}
            {!formValid && !ageValid && (
              <i className={s.error_age}>{formErrors.age}</i>
            )}
            {!formValid && !emailValid && (
              <i className={s.error_email}>{formErrors.email}</i>
            )}
            {!formValid && !passwordValid && (
              <i className={s.error_password}>{formErrors.password}</i>
            )}
            <i className={s.error}>{errorRePassword || ''}</i>
            {errorMessage && (
              <i className={s.error}>
                ,     i i.  ii...
              </i>
            )}
            <div className={s.box_btn}>
              <button type="button" onClick={this.handleCloseModal}>
                
              </button>
              <button type="submit" disabled={!formValid}>
                
              </button>
            </div>
          </form>
          <IconsAvatar
            className={s.user_image_component}
            changeAvatar={this.changeUserPic}
          />
        </div>
      </div>
    );
  }
}

const mapStateToProps = store => ({
  errorMessage: getErrorMessageRegistration(store),
});

const mapDispatchToProps = {
  onSignUp: signupOperation,
  closeModal,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ModalRegistration);
